<template>
	<div>
		<h3>買い物リスト</h3>
		<button v-on:click="goPageAdd">買うもの追加</button>
		
		<div v-for="(todo,i) in todos"  v-bind:key="i">
            <img class="img" src="./peach.png" width="20" />
            <img v-bind:src="imgPath">
			{{todo}}
			<input type="checkbox" />
			<button v-on:click="deleteTodo(i)">削除</button>
		</div>
		<br>
		<button v-on:click="goPageFavorite">お気に入り</button>
		<button v-on:click="goPageCheckList">買い物リスト</button>
		<button v-on:click="goPageHistory">買い物履歴</button>
	</div>
</template>
<script>
	export default{
		data(){
			return{todos:[],
					imgPath: require('@/assets/pizza.png')}
		},
		created(){
			this.todos=JSON.parse(window.localStorage.getItem('todos'))||[];
		},
		methods:{
			deleteTodo(i){
				this.todos.splice(i,1);
				window.localStorage.setItem('todos',JSON.stringify(this.todos));
			},
			goPageAdd(){
				this.$router.push('/todos/add');
			},
			goPageFavorite(){
				this.$router.push('/todos/favorite');
			},
			goPageCheckList(){
				this.$router.push('/');
			},
			goPageHistory(){
				this.$router.push('/todos/history');
			}
		}
	}
</script>